</template>

<script>
import axios from 'axios'
import {
  validateName,
  validateEmail
} from '../utils/validations'

export default {
  name: 'top',
  data () {
    return {
      name: '',
      email: '',
      message: '',
      messageFlag: false,
      errorFlag: false,
    }
  },
  created () {
    this.name = localStorage.getItem('name')
    this.email = localStorage.getItem('email')
  },
  methods: {
    inputName () {
      localStorage.setItem('name', this.name)
      this.messageFlag = false
    },
    inputEmail () {
      localStorage.setItem('email', this.email)
      this.messageFlag = false
    },
    enter () {
      this.errorFlag = validateName(this.name) || validateEmail(this.email)
      const data = {
        'name': this.name,
        'email': this.email
      }
      const headers = {
        'Content-Type': 'application/json'
      }
      !this.errorFlag ? axios.post('https://6lyp6am9ni.execute-api.ap-northeast-1.amazonaws.com/prod/ekk_send_form?name=' + this.name, { data: data }, { headers: headers}).then().catch(err => {
        console.log(err)
        this.errorFlag = true
      }) : null
      this.message = this.errorFlag ? '' : ''
      this.messageFlag = true
      this.errorFlag ? null : this.clear()
    },
    clear () {
      localStorage.clear()
      this.name = ''
      this.email = ''
    }
  }
}
</script>

